


/*
*  setHtmlFontsize HTML
*   n number
*/
var setHtmlFontsize = function(n){
    var oHmtl = document.querySelector('html'),
        nWidth = window.screen.availWidth;
        oHmtl.style.fontSize = nWidth / n + 'px';
}

module.exports = {
    setHtmlFontsize:setHtmlFontsize
}


